'use client';

import React, { useState } from 'react';
import { useRouter } from 'next/navigation';
import { toast } from 'react-toastify';

const SignInPage = () => {
    const [form, setForm] = useState({ email: '', password: '' });
    const router = useRouter();

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setForm(prev => ({ ...prev, [e.target.name]: e.target.value }));
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        // ðŸ”¥ No backend, so we simulate login
        if (form.email === 'demo@example.com' && form.password === 'password') {
            toast.success('Login successful!');

            // In real app: call backend and set context user
            // For now: just navigate to home
            router.push('/');
        } else {
            toast.error('Invalid credentials');
        }
    };

    return (
        <div className="flex items-center justify-center min-h-screen bg-gray-100">
            <form
                onSubmit={handleSubmit}
                className="bg-white p-8 rounded-xl shadow-md w-96 space-y-4"
            >
                <h2 className="text-2xl font-bold text-center">Sign In</h2>

                <input
                    name="email"
                    type="email"
                    placeholder="Email"
                    value={form.email}
                    onChange={handleChange}
                    required
                    className="w-full p-2 border rounded-md text-black focus:outline-none focus:ring-2"
                />

                <input
                    name="password"
                    type="password"
                    placeholder="Password"
                    value={form.password}
                    onChange={handleChange}
                    required
                    className="w-full p-2 border rounded-md text-black focus:outline-none focus:ring-2"
                />

                <button
                    type="submit"
                    className="w-full bg-green-600 text-white p-2 rounded-md hover:bg-green-700 transition"
                >
                    Log In
                </button>
            </form>
        </div>
    );
};

export default SignInPage;
